import Vue from 'vue';
import i18n from '../../../i18n';
import XFolder from './drive.folder.vue';
import XFile from './drive.file.vue';
import XFileDetail from './drive.file-detail.vue';
export default Vue.extend({
    i18n: i18n('mobile/views/components/drive.vue'),
    components: {
        XFolder,
        XFile,
        XFileDetail
    },
    props: ['initFolder', 'initFile', 'selectFile', 'multiple', 'isNaked', 'top'],
    data() {
        return {
            /**
             * ()
             * * null 
             */
            folder: null,
            file: null,
            files: [],
            folders: [],
            moreFiles: false,
            moreFolders: false,
            hierarchyFolders: [],
            selectedFiles: [],
            info: null,
            connection: null,
            fetching: true,
            fetchingMoreFiles: false,
            fetchingMoreFolders: false
        };
    },
    computed: {
        isFileSelectMode() {
            return this.selectFile;
        }
    },
    watch: {
        top() {
            if (this.isNaked) {
                this.$refs.nav.style.top = `${this.top}px`;
            }
        }
    },
    mounted() {
        this.connection = this.$root.stream.useSharedConnection('drive');
        this.connection.on('fileCreated', this.onStreamDriveFileCreated);
        this.connection.on('fileUpdated', this.onStreamDriveFileUpdated);
        this.connection.on('fileDeleted', this.onStreamDriveFileDeleted);
        this.connection.on('folderCreated', this.onStreamDriveFolderCreated);
        this.connection.on('folderUpdated', this.onStreamDriveFolderUpdated);
        if (this.initFolder) {
            this.cd(this.initFolder, true);
        }
        else if (this.initFile) {
            this.cf(this.initFile, true);
        }
        else {
            this.fetch();
        }
        if (this.isNaked) {
            this.$refs.nav.style.top = `${this.top}px`;
        }
    },
    beforeDestroy() {
        this.connection.dispose();
    },
    methods: {
        onStreamDriveFileCreated(file) {
            this.addFile(file, true);
        },
        onStreamDriveFileUpdated(file) {
            const current = this.folder ? this.folder.id : null;
            if (current != file.folderId) {
                this.removeFile(file);
            }
            else {
                this.addFile(file, true);
            }
        },
        onStreamDriveFileDeleted(fileId) {
            this.removeFile(fileId);
        },
        onStreamDriveFolderCreated(folder) {
            this.addFolder(folder, true);
        },
        onStreamDriveFolderUpdated(folder) {
            const current = this.folder ? this.folder.id : null;
            if (current != folder.parentId) {
                this.removeFolder(folder);
            }
            else {
                this.addFolder(folder, true);
            }
        },
        dive(folder) {
            this.hierarchyFolders.unshift(folder);
            if (folder.parent)
                this.dive(folder.parent);
        },
        cd(target, silent = false) {
            if (target == null) {
                this.goRoot(silent);
                return;
            }
            else if (typeof target == 'object') {
                target = target.id;
            }
            this.file = null;
            this.fetching = true;
            this.$root.api('drive/folders/show', {
                folderId: target
            }).then(folder => {
                this.folder = folder;
                this.hierarchyFolders = [];
                if (folder.parent)
                    this.dive(folder.parent);
                this.$emit('open-folder', this.folder, silent);
                this.fetch();
            });
        },
        addFolder(folder, unshift = false) {
            const current = this.folder ? this.folder.id : null;
            // 
            if (current != folder.parentId)
                return;
            // 
            if (this.folders.some(f => f.id == folder.id))
                return;
            if (unshift) {
                this.folders.unshift(folder);
            }
            else {
                this.folders.push(folder);
            }
        },
        addFile(file, unshift = false) {
            const current = this.folder ? this.folder.id : null;
            // 
            if (current != file.folderId)
                return;
            if (this.files.some(f => f.id == file.id)) {
                const exist = this.files.map(f => f.id).indexOf(file.id);
                Vue.set(this.files, exist, file);
                return;
            }
            if (unshift) {
                this.files.unshift(file);
            }
            else {
                this.files.push(file);
            }
        },
        removeFolder(folder) {
            if (typeof folder == 'object')
                folder = folder.id;
            this.folders = this.folders.filter(f => f.id != folder);
        },
        removeFile(file) {
            if (typeof file == 'object')
                file = file.id;
            this.files = this.files.filter(f => f.id != file);
        },
        appendFile(file) {
            this.addFile(file);
        },
        appendFolder(folder) {
            this.addFolder(folder);
        },
        prependFile(file) {
            this.addFile(file, true);
        },
        prependFolder(folder) {
            this.addFolder(folder, true);
        },
        goRoot(silent = false) {
            // root
            if (this.folder == null && this.file == null)
                return;
            this.file = null;
            this.folder = null;
            this.hierarchyFolders = [];
            this.$emit('move-root', silent);
            this.fetch();
        },
        fetch() {
            this.folders = [];
            this.files = [];
            this.moreFolders = false;
            this.moreFiles = false;
            this.fetching = true;
            this.$emit('begin-fetch');
            let fetchedFolders = null;
            let fetchedFiles = null;
            const foldersMax = 20;
            const filesMax = 20;
            // 
            this.$root.api('drive/folders', {
                folderId: this.folder ? this.folder.id : null,
                limit: foldersMax + 1
            }).then(folders => {
                if (folders.length == foldersMax + 1) {
                    this.moreFolders = true;
                    folders.pop();
                }
                fetchedFolders = folders;
                complete();
            });
            // 
            this.$root.api('drive/files', {
                folderId: this.folder ? this.folder.id : null,
                limit: filesMax + 1
            }).then(files => {
                if (files.length == filesMax + 1) {
                    this.moreFiles = true;
                    files.pop();
                }
                fetchedFiles = files;
                complete();
            });
            let flag = false;
            const complete = () => {
                if (flag) {
                    for (const x of fetchedFolders)
                        this.appendFolder(x);
                    for (const x of fetchedFiles)
                        this.appendFile(x);
                    this.fetching = false;
                    // 
                    this.$emit('fetched');
                }
                else {
                    flag = true;
                    // 
                    this.$emit('fetch-mid');
                }
            };
            if (this.folder == null) {
                // Fetch addtional drive info
                this.$root.api('drive').then(info => {
                    this.info = info;
                });
            }
        },
        fetchMoreFiles() {
            this.fetching = true;
            this.fetchingMoreFiles = true;
            const max = 30;
            // 
            this.$root.api('drive/files', {
                folderId: this.folder ? this.folder.id : null,
                limit: max + 1,
                untilId: this.files[this.files.length - 1].id
            }).then(files => {
                if (files.length == max + 1) {
                    this.moreFiles = true;
                    files.pop();
                }
                else {
                    this.moreFiles = false;
                }
                for (const x of files)
                    this.appendFile(x);
                this.fetching = false;
                this.fetchingMoreFiles = false;
            });
        },
        chooseFile(file) {
            if (this.isFileSelectMode) {
                if (this.multiple) {
                    if (this.selectedFiles.some(f => f.id == file.id)) {
                        this.selectedFiles = this.selectedFiles.filter(f => f.id != file.id);
                    }
                    else {
                        this.selectedFiles.push(file);
                    }
                    this.$emit('change-selection', this.selectedFiles);
                }
                else {
                    this.$emit('selected', file);
                }
            }
            else {
                this.cf(file);
            }
        },
        cf(file, silent = false) {
            if (typeof file == 'object')
                file = file.id;
            this.fetching = true;
            this.$root.api('drive/files/show', {
                fileId: file
            }).then(file => {
                this.file = file;
                this.folder = null;
                this.hierarchyFolders = [];
                if (file.folder)
                    this.dive(file.folder);
                this.fetching = false;
                this.$emit('open-file', this.file, silent);
            });
        },
        selectLocalFile() {
            this.$refs.file.click();
        },
        createFolder() {
            this.$root.dialog({
                title: this.$t('folder-name'),
                input: {
                    default: this.folder.name
                }
            }).then(({ result: name }) => {
                if (!name) {
                    this.$root.dialog({
                        type: 'error',
                        text: this.$t('folder-name-cannot-empty')
                    });
                    return;
                }
                this.$root.api('drive/folders/create', {
                    name: name,
                    parentId: this.folder ? this.folder.id : undefined
                }).then(folder => {
                    this.addFolder(folder, true);
                });
            });
        },
        renameFolder() {
            if (this.folder == null) {
                this.$root.dialog({
                    type: 'error',
                    text: this.$t('here-is-root')
                });
                return;
            }
            this.$root.dialog({
                title: this.$t('folder-name'),
                input: {
                    default: this.folder.name
                }
            }).then(({ result: name }) => {
                if (!name) {
                    this.$root.dialog({
                        type: 'error',
                        text: this.$t('cannot-empty')
                    });
                    return;
                }
                this.$root.api('drive/folders/update', {
                    name: name,
                    folderId: this.folder.id
                }).then(folder => {
                    this.cd(folder);
                });
            });
        },
        moveFolder() {
            if (this.folder == null) {
                this.$root.dialog({
                    type: 'error',
                    text: this.$t('here-is-root')
                });
                return;
            }
            this.$chooseDriveFolder().then(folder => {
                this.$root.api('drive/folders/update', {
                    parentId: folder ? folder.id : null,
                    folderId: this.folder.id
                }).then(folder => {
                    this.cd(folder);
                });
            });
        },
        urlUpload() {
            const url = window.prompt(this.$t('url-prompt'));
            if (url == null || url == '')
                return;
            this.$root.api('drive/files/upload_from_url', {
                url: url,
                folderId: this.folder ? this.folder.id : undefined
            });
            this.$root.dialog({
                type: 'info',
                text: this.$t('uploading')
            });
        },
        onChangeLocalFile() {
            for (const f of Array.from(this.$refs.file.files)) {
                this.$refs.uploader.upload(f, this.folder);
            }
        },
        deleteFolder() {
            if (this.folder == null) {
                this.$root.dialog({
                    type: 'error',
                    text: this.$t('here-is-root')
                });
                return;
            }
            this.$root.api('drive/folders/delete', {
                folderId: this.folder.id
            }).then(folder => {
                this.cd(this.folder.parentId);
            });
        }
    }
});
